/**
 * @fileoverview This file defines the Announcements page for the HR portal.
 * It provides HR personnel with tools to create, view, and manage company-wide announcements.
 */
'use client';
import * as React from 'react';
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
  CardFooter
} from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { MoreHorizontal } from "lucide-react";
import {
    DropdownMenu,
    DropdownMenuContent,
    DropdownMenuItem,
    DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { getPastAnnouncements } from "@/lib/data";
import {
    AlertDialog,
    AlertDialogAction,
    AlertDialogCancel,
    AlertDialogContent,
    AlertDialogDescription,
    AlertDialogFooter,
    AlertDialogHeader,
    AlertDialogTitle,
    AlertDialogTrigger,
} from "@/components/ui/alert-dialog";

/**
 * Renders the HR announcements management page.
 * Allows creating, viewing, and managing company-wide announcements.
 * @returns {JSX.Element} The announcements page component.
 */
export default function AnnouncementsPage() {
    const [pastAnnouncements, setPastAnnouncements] = React.useState([]);

    React.useEffect(() => {
        async function fetchAnnouncements() {
            const data = await getPastAnnouncements();
            setPastAnnouncements(data);
        }
        fetchAnnouncements();
    }, []);


  return (
    <div className="space-y-6">
      <h1 className="text-3xl font-bold font-headline">Company Announcements</h1>
      
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* New Announcement Form */}
        <div className="lg:col-span-1">
            <Card>
                <CardHeader>
                    <CardTitle>New Announcement</CardTitle>
                    <CardDescription>Create a new announcement for all employees.</CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                    <div className="space-y-2">
                        <Label htmlFor="announcement-title">Title</Label>
                        <Input id="announcement-title" placeholder="Enter the title" />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="announcement-content">Content</Label>
                        <Textarea id="announcement-content" placeholder="Type your announcement here..." className="min-h-[200px]" />
                    </div>
                </CardContent>
                <CardFooter className="flex justify-between">
                    {/* Actions to save as draft or post immediately */}
                    <AlertDialog>
                        <AlertDialogTrigger asChild>
                            <Button variant="outline">Save Draft</Button>
                        </AlertDialogTrigger>
                         <AlertDialogContent>
                            <AlertDialogHeader>
                                <AlertDialogTitle>Are you sure you want to save as draft?</AlertDialogTitle>
                            </AlertDialogHeader>
                            <AlertDialogFooter>
                                <AlertDialogCancel>Cancel</AlertDialogCancel>
                                <AlertDialogAction>Save Draft</AlertDialogAction>
                            </AlertDialogFooter>
                        </AlertDialogContent>
                    </AlertDialog>
                    <AlertDialog>
                        <AlertDialogTrigger asChild>
                           <Button>Post Announcement</Button>
                        </AlertDialogTrigger>
                         <AlertDialogContent>
                            <AlertDialogHeader>
                                <AlertDialogTitle>Are you sure you want to post this announcement?</AlertDialogTitle>
                                <AlertDialogDescription>
                                    This will make the announcement visible to all employees.
                                </AlertDialogDescription>
                            </AlertDialogHeader>
                            <AlertDialogFooter>
                                <AlertDialogCancel>Cancel</AlertDialogCancel>
                                <AlertDialogAction>Post</AlertDialogAction>
                            </AlertDialogFooter>
                        </AlertDialogContent>
                    </AlertDialog>
                </CardFooter>
            </Card>
        </div>

        {/* Past Announcements Table */}
        <div className="lg:col-span-2">
            <Card>
                <CardHeader>
                    <CardTitle>Past Announcements</CardTitle>
                    <CardDescription>Manage and view previous announcements.</CardDescription>
                </CardHeader>
                <CardContent>
                    {pastAnnouncements.length > 0 ? (
                        <Table>
                            <TableHeader>
                                <TableRow>
                                    <TableHead>Title</TableHead>
                                    <TableHead>Posted By</TableHead>
                                    <TableHead>Date</TableHead>
                                    <TableHead>Status</TableHead>
                                    <TableHead className="text-right">Actions</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {pastAnnouncements.map(a => (
                                    <TableRow key={a.id}>
                                        <TableCell className="font-medium">{a.title}</TableCell>
                                        <TableCell>{a.postedBy}</TableCell>
                                        <TableCell>{a.date}</TableCell>
                                        <TableCell><Badge variant={a.status === 'Published' ? 'default' : 'secondary'} className={a.status === 'Published' ? 'bg-blue-500' : ''}>{a.status}</Badge></TableCell>
                                        <TableCell className="text-right">
                                            {/* Dropdown menu for actions like edit, unpublish, delete */}
                                            <DropdownMenu>
                                                <DropdownMenuTrigger asChild>
                                                    <Button variant="ghost" className="h-8 w-8 p-0">
                                                        <MoreHorizontal />
                                                    </Button>
                                                </DropdownMenuTrigger>
                                                <DropdownMenuContent align="end">
                                                    <DropdownMenuItem>Edit</DropdownMenuItem>
                                                    <DropdownMenuItem>Unpublish</DropdownMenuItem>
                                                    <DropdownMenuItem className="text-red-600">Delete</DropdownMenuItem>
                                                </DropdownMenuContent>
                                            </DropdownMenu>
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    ) : (
                        <p className="text-muted-foreground text-center">No past announcements found.</p>
                    )}
                </CardContent>
            </Card>
        </div>
      </div>
    </div>
  );
}
